import '../ControlPanelStyle.css';
import ReactDOM from 'react-dom';
import Start from '../Start';
import firebase from 'firebase';

var ChangePass;
var PassChange;
var oldpassword;
var password;
var passwordrep;
var Data;
export function PwChange(key) {
  return(
    oldpassword = document.getElementById('password-old').value,
    password = document.getElementById('password').value,
    passwordrep = document.getElementById('password-rep').value,
    ChangePass = firebase.database().ref("User").orderByKey().equalTo(key),
    ChangePass.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
      });
      if(oldpassword === password){
        if(oldpassword === Data.password){
          if(password === passwordrep){
            if((password.length >= 8)&&(password.length <= 20)){
              PassChange = firebase.database().ref("User/" + key);
              PassChange.update({
                password: password
              });
              document.getElementById("change-pw").style.color = "green";
              document.getElementById("change-pw").innerHTML = "Slaptažodis sėkmingai pakeistas";
            }
            else document.getElementById("change-pw").innerHTML = "Slaptažodis turi buti nuo 8 iki 20 simbolių";
          }
          else document.getElementById("change-pw").innerHTML = "Slaptažodžiai nesutampa";
        }
        else document.getElementById("change-pw").innerHTML = "Neteisingas slaptažodis";
      }
      else document.getElementById("change-pw").innerHTML = "Seno ir naujo slaptažodio laukeliai sutampa";
    })  
  );  
};

function ChangePassword(props) {
  return (
    <div className="dashboard-page">
      <h2>Slaptažodžio keitimas</h2>
      <div>
        <div className="name">Dabartinis slaptažodis</div>
        <input id="old-password" placeholder="Dabartinis slaptažodis" className="input-box" type="text" maxLength="20" />
      </div>
      <div>
        <div className="password">Naujas slaptažodis</div>
        <input id="password" placeholder="Naujas slaptažodis" className="input-box " type="password" maxLength="20" />
      </div>
      <div>
        <div className="password">Pakartoti slaptažodį</div>
        <input id="password-rep" placeholder="Naujas slaptažodis" className="input-box pw" type="password" maxLength="20" />
      </div>
      <div id="change-pw"></div>
      <div className="buttons">
        <button id="autobutton" className="menubutton" onClick={() => PwChange(props.id)}>Registruotis</button>
        <button className="menubutton" onClick={() => ReactDOM.render(<Start />, document.getElementById('root'))}>Grįžti atgal</button>
      </div>
    </div>
  );
}

export default ChangePassword;