import * as React from 'react';
import firebase from 'firebase';
import ReactDOM from 'react-dom';
import '../ControlPanelStyle.css';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';

const tableIcons = {
  Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
  Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
  Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
  DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
  Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
  Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
  FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
  LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
  NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
  PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
  ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
  Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
  SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
  ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
  ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
};

var row;
var GetTableData;
var Data;
var array = [];
var array1 = [];
var array2 = [];
var array3 = [];
var rows = [];
var rows1 = [];
var rows2 = [];
var rows3 = [];
var todayd = new Date();
var today = "";
today = today + todayd.getFullYear() + "-";
if(todayd.getMonth() < 10){today = today + "0" + (todayd.getMonth() + 1) + "-";}
else{ today = today + (todayd.getMonth() + 1) + "-";}
if(todayd.getDate() < 10){today = today + "0" + todayd.getDate();}
else{ today = today + todayd.getDate();}

function Current(){
  return(
    document.getElementById("tab-table-1").style.display = "block",
    document.getElementById("tab-table-2").style.display = "none",
    document.getElementById("tab-table-3").style.display = "none",
    document.getElementById("tab-table-4").style.display = "none"
    );
};
function Unconfirmed(){
  return(
    document.getElementById("tab-table-1").style.display = "none",
    document.getElementById("tab-table-2").style.display = "block",
    document.getElementById("tab-table-3").style.display = "none",
    document.getElementById("tab-table-4").style.display = "none"
  );
};
function Confirmed(){
  return(
    document.getElementById("tab-table-1").style.display = "none",
    document.getElementById("tab-table-2").style.display = "none",
    document.getElementById("tab-table-3").style.display = "block",
    document.getElementById("tab-table-4").style.display = "none"
  );
};
function Passed(){
  return(
    document.getElementById("tab-table-1").style.display = "none",
    document.getElementById("tab-table-2").style.display = "none",
    document.getElementById("tab-table-3").style.display = "none",
    document.getElementById("tab-table-4").style.display = "block"
  );
};

export default function PastVacationTable(props) {
  const { useState } = React;
  const [selectedRow, setSelectedRow] = useState(null);
  return (
    array.length = 0,
    array1.length = 0,
    array2.length = 0,
    array3.length = 0,
    GetTableData = firebase.database().ref("Vacations").orderByChild("user").equalTo(props.id),
    GetTableData.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        Data = childSnapshot.val();
        if((Data.endDate >= today)&&(Data.startDate <= today)&&(Data.status === 3)){
          row ={
            type: Data.type,
            date: Data.date,
            startdate: Data.startDate,
            enddate: Data.endDate,
            duration: Data.duration
          };
          array.push(row);
        }
        if(Data.status !== 3){
          row ={
            type: Data.type,
            date: Data.date,
            startdate: Data.startDate,
            enddate: Data.endDate,
            duration: Data.duration
          };
          array1.push(row);
        }
        if((Data.startDate > today)&&(Data.status === 3)){
          row ={
            type: Data.type,
            date: Data.date,
            startdate: Data.startDate,
            enddate: Data.endDate,
            duration: Data.duration
          };
          array2.push(row);
        }
        if((Data.endDate <= today)&&(Data.status === 3)){
          row ={
            type: Data.type,
            date: Data.date,
            startdate: Data.startDate,
            enddate: Data.endDate,
            duration: Data.duration
          };
          array3.push(row);
        }
      });
      rows = array.map((arr) => arr);
      rows1 = array1.map((arr) => arr);
      rows2 = array2.map((arr) => arr);
      rows3 = array3.map((arr) => arr);
    }),
  <div className="tab-tables">
    <div><h2>Mano atostogos</h2><br/></div>
    <div>
      <button className="tab-buttons" onClick={() => Current()}>Vykstančios</button>
      <button className="tab-buttons" onClick={() => Unconfirmed()}>Nepatvirtintos</button>
      <button className="tab-buttons" onClick={() => Confirmed()}>Patvirtintos</button>
      <button className="tab-buttons" onClick={() => Passed()}>Pasibaigusios</button>
    </div>
    <div id="tab-table-1" className="tab-table-1" style={{ maxWidth: '100%' }}>
      <MaterialTable
        icons={tableIcons}
        localization={{
          pagination: {
            labelRowsSelect: 'eilutės',
            firstTooltip: 'Pirmas puslapis',
            nextTooltip: 'Sekantis puslapis',
            previousTooltip: 'Ankstesnis puslapis',
            lastTooltip: 'Paskutinis puslapis'
          },
          toolbar: {
            searchPlaceholder: 'Ieškoti',
            searchTooltip: 'Ieškoti'
          },
          body: {
            emptyDataSourceMessage: 'Nėra įkeltų duomenų',
          }
      }}
        columns={[
          { title: 'TIPAS', field: 'type', editable: 'never' },
          { title: 'PATEIKIMO DATA', field: 'date', editable: 'never' },
          { title: 'PRADŽIOS DATA', field: 'startdate', editable: 'never' },
          { title: 'PABAIGOS DATA', field: 'enddate', editable: 'never' },
          { title: 'TRUKMĖ', field: 'duration', editable: 'never' }
        ]}
        data={rows}
        title="Vykstančios atostogos"
        onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
        options={{
          search: false,
          actionsColumnIndex: -1,
          pageSizeOptions: [5, 10],
          pageSize: 5,
          addRowPosition: 'first',
          headerStyle: {
            fontWeight: 'bold',
            fontSize: '16px'
          },
          titleStyle:{
            fontWeight: 'bold'
          },
          rowStyle: rowData => ({
            backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
          })
        }}
      />
    </div>
    <div id="tab-table-2" className="tab-table-2" style={{ maxWidth: '100%' }}>
      <MaterialTable
        icons={tableIcons}
        localization={{
          pagination: {
            labelRowsSelect: 'eilutės',
            firstTooltip: 'Pirmas puslapis',
            nextTooltip: 'Sekantis puslapis',
            previousTooltip: 'Ankstesnis puslapis',
            lastTooltip: 'Paskutinis puslapis'
          },
          toolbar: {
            searchPlaceholder: 'Ieškoti',
            searchTooltip: 'Ieškoti'
          },
          body: {
            emptyDataSourceMessage: 'Nėra įkeltų duomenų',
          }
      }}
        columns={[
          { title: 'TIPAS', field: 'type', editable: 'never' },
          { title: 'PATEIKIMO DATA', field: 'date', editable: 'never' },
          { title: 'PRADŽIOS DATA', field: 'startdate', editable: 'never' },
          { title: 'PABAIGOS DATA', field: 'enddate', editable: 'never' },
          { title: 'TRUKMĖ', field: 'duration', editable: 'never' }
        ]}
        data={rows1}
        title="Nepatvirtintos atostogos"
        onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
        options={{
          search: false,
          actionsColumnIndex: -1,
          pageSizeOptions: [5, 10],
          pageSize: 5,
          addRowPosition: 'first',
          headerStyle: {
            fontWeight: 'bold',
            fontSize: '16px'
          },
          titleStyle:{
            fontWeight: 'bold'
          },
          rowStyle: rowData => ({
            backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
          })
        }}
      />
    </div>
    <div id="tab-table-3" className="tab-table-3" style={{ maxWidth: '100%' }}>
      <MaterialTable
        icons={tableIcons}
        localization={{
          pagination: {
            labelRowsSelect: 'eilutės',
            firstTooltip: 'Pirmas puslapis',
            nextTooltip: 'Sekantis puslapis',
            previousTooltip: 'Ankstesnis puslapis',
            lastTooltip: 'Paskutinis puslapis'
          },
          toolbar: {
            searchPlaceholder: 'Ieškoti',
            searchTooltip: 'Ieškoti'
          },
          body: {
            emptyDataSourceMessage: 'Nėra įkeltų duomenų',
          }
      }}
        columns={[
          { title: 'TIPAS', field: 'type', editable: 'never' },
          { title: 'PATEIKIMO DATA', field: 'date', editable: 'never' },
          { title: 'PRADŽIOS DATA', field: 'startdate', editable: 'never' },
          { title: 'PABAIGOS DATA', field: 'enddate', editable: 'never' },
          { title: 'TRUKMĖ', field: 'duration', editable: 'never' }
        ]}
        data={rows2}
        title="Patvirtintos atostogos"
        onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
        options={{
          search: false,
          actionsColumnIndex: -1,
          pageSizeOptions: [5, 10],
          pageSize: 5,
          addRowPosition: 'first',
          headerStyle: {
            fontWeight: 'bold',
            fontSize: '16px'
          },
          titleStyle:{
            fontWeight: 'bold'
          },
          rowStyle: rowData => ({
            backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
          })
        }}
      />
    </div>
    <div id="tab-table-4" className="tab-table-4" style={{ maxWidth: '100%' }}>
      <MaterialTable
        icons={tableIcons}
        localization={{
          pagination: {
            labelRowsSelect: 'eilutės',
            firstTooltip: 'Pirmas puslapis',
            nextTooltip: 'Sekantis puslapis',
            previousTooltip: 'Ankstesnis puslapis',
            lastTooltip: 'Paskutinis puslapis'
          },
          toolbar: {
            searchPlaceholder: 'Ieškoti',
            searchTooltip: 'Ieškoti'
          },
          body: {
            emptyDataSourceMessage: 'Nėra įkeltų duomenų',
          }
      }}
        columns={[
          { title: 'TIPAS', field: 'type', editable: 'never' },
          { title: 'PATEIKIMO DATA', field: 'date', editable: 'never' },
          { title: 'PRADŽIOS DATA', field: 'startdate', editable: 'never' },
          { title: 'PABAIGOS DATA', field: 'enddate', editable: 'never' },
          { title: 'TRUKMĖ', field: 'duration', editable: 'never' }
        ]}
        data={rows3}
        title="Pasibaigusios atostogos"
        onRowClick={((evt, selectedRow) => setSelectedRow(selectedRow.tableData.id))}
        options={{
          search: false,
          actionsColumnIndex: -1,
          pageSizeOptions: [5, 10],
          pageSize: 5,
          addRowPosition: 'first',
          headerStyle: {
            fontWeight: 'bold',
            fontSize: '16px'
          },
          titleStyle:{
            fontWeight: 'bold'
          },
          rowStyle: rowData => ({
            backgroundColor: (selectedRow === rowData.tableData.id) ? '#EEE' : '#FFF'
          })
        }}
      />
    </div>
    <button className="renew-button" onClick={() => ReactDOM.render(<PastVacationTable id={props.id}/>, document.getElementById('panelroot'))}>Įkelti / Atnaujinti duomenis</button>
  </div>
  );
}